export const Bookshelf = defineDocumentType(() => ({
  name: 'Bookshelf',
  filePathPattern: 'bookshelf/**/*.md',
  contentType: 'mdx',
  fields: {
    title: { type: 'string' },
    author: { type: 'string' },
    description: { type: 'string' },
    image: { type: 'string' },
    category: { type: 'string' },
    readingPeriod: { type: 'string' },
    rating: { type: 'number' },
    order: { type: 'number' },
    isPage: { type: 'boolean', default: false },
    draft: { type: 'boolean' },
  },
  computedFields: {
    ...computedFields,
    slug: {
      type: 'string',
      resolve: (doc) => slugify(doc._raw.sourceFileName.replace(/\.mdx?$/, '')),
    },
    excerpt: {
      type: 'string',
      resolve: (doc) => createExcerpt(doc.body.raw),
    },
    firstImage: {
      type: 'string',
      resolve: (doc) => doc.image || extractFirstImage(doc.body.raw) || null,
    },
  },
}))

